import axios from 'axios'
import store from '../store'
import router from '../router'
import {
  Message,
  Loading
} from 'element-ui'
let loading;

function startLoading() { //Element loading-start  
  loading = Loading.service({
    lock: true,
    text: 'Loding',
    background: 'rgba(0,0,0,0.6)'
  })
}

function endLoading() { //Element loading-close  
  loading.close()
}
//  
const BASEAPI = process.env.NODE_ENV === 'development' ? 'http://114.115.211.170:8080/web/' : 'http://114.115.211.170:8080/web/'
// axios
const request = axios.create({
  baseURL: BASEAPI, //
  timeout: 60000, // 
  withCredentials: false, //axios cookies sessiontrue false
  headers: {
    'content-type': 'application/json'
  } //
});
// 
request.interceptors.request.use(config => {
  startLoading();
  if (store.state.token) {
    // console.log(store.state.token)
    config.headers.token = `${store.state.token}`;
  } // vuextoken 
  return config;
}, error => {
  return Promise.reject(error);
});
//
request.interceptors.response.use(response => {
  //
  endLoading()
  //token/essionlogin 
  if (response.data.code == -30) {
    router.push('/login')  
  }
  return response;
}, error => {
  endLoading()
  console.log(error)
  return Promise.reject(error);
});

export default request



